main {
  flex-grow: 1;
}

 .film{
 padding-top: 28px;
  padding-bottom: 40px;
   @include tablet{
  padding-top: 50px;
  padding-bottom: 60px; 
   }
   
}


.film__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
  margin: 0 auto;
  @include tablet {
    grid-template-columns: repeat(2, 1fr);
  }
  @include desktop {
    grid-template-columns: repeat(3, 1fr);
  }

  a {
    display: block;
  }
}

.film__img-thumb {
  height: 398px;
  min-width: 280px;
  margin-bottom: 10px;
  @include tablet{
     width: 294px;
  }
  @include desktop{
     width: 274px;
  }
}

.film__name{
font-family: $ffMedium;
font-size: var(--font-size12);
line-height: 1.6;
text-transform: uppercase;
color: $black;
}

.film__text{
font-family: $ffMedium;;
font-size: var(--font-size12);
line-height: 1.33;
color: $orange;
}